import React, {useState} from 'react';
import LoginForm from "../components/LoginForm";
import RegisterForm from "../components/RegisterForm";
import {useSelector} from "react-redux";
import {Navigate} from "react-router-dom";

export default function Welcome() {
    const [isLoginView, setIsLogin] = useState(true);
    const user = useSelector(({auth}) => auth.user);

    const optInText = isLoginView ? ["Need an account?", "Register"] : ["Have an account?", "Login"];

    if(user) {
        return <Navigate to="/home"/>
    }

    return (
        <div className="centered-view">
            <div className="centered-container">
                { isLoginView ? <LoginForm/> : <RegisterForm/> }
                <small className="form-text text-muted mt-2">{optInText[0]}
                    <span
                        onClick={() => setIsLogin(!isLoginView)}
                        className="btn-link ml-2 margin-h10 pointer">{optInText[1]}</span></small>
            </div>
        </div>
    )
}